.achievements-page{padding:4rem 0;min-height:100vh}.achievements-page .page-title{font-size:3rem;margin-bottom:1rem;text-align:center;background:linear-gradient(135deg, #4F46E5 0%, #7C3AED 100%);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0)}.achievements-page .page-subtitle{text-align:center;color:#94a3b8;margin-bottom:3rem}.achievements-page .achievements-list{display:flex;flex-direction:column;gap:3rem}.achievements-page .achievement-card{background:rgba(30,41,59,.3);border-radius:16px;padding:2rem;backdrop-filter:blur(10px);border:1px solid rgba(226,232,240,.1);box-shadow:0 10px 15px -3px rgba(0,0,0,.1),0 4px 6px -2px rgba(0,0,0,.05);transition:transform .3s ease}.achievements-page .achievement-card:hover{transform:translateY(-5px)}.achievements-page .achievement-card .achievement-content{padding-right:2rem}@media(max-width: 768px){.achievements-page .achievement-card .achievement-content{padding-right:0;margin-bottom:2rem}}.achievements-page .achievement-card .achievement-header{margin-bottom:1.5rem}.achievements-page .achievement-card .achievement-header h2{font-size:2rem;margin-bottom:1rem;background:linear-gradient(135deg, #38BDF8 0%, #3B82F6 100%);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0)}.achievements-page .achievement-card .achievement-header .achievement-meta{display:flex;gap:1.5rem;color:#94a3b8}.achievements-page .achievement-card .achievement-header .achievement-meta .year,.achievements-page .achievement-card .achievement-header .achievement-meta .location{display:flex;align-items:center;gap:.5rem}.achievements-page .achievement-card .description{color:#94a3b8;margin-bottom:2rem;line-height:1.8}.achievements-page .achievement-card .highlights h3{color:#38bdf8;font-size:1.2rem;margin-bottom:1rem}.achievements-page .achievement-card .highlights ul{list-style:none;padding:0;margin:0}.achievements-page .achievement-card .highlights ul li{color:#94a3b8;margin-bottom:.75rem;padding-left:1.5rem;position:relative}.achievements-page .achievement-card .highlights ul li::before{content:"â€¢";color:#3b82f6;position:absolute;left:0}.achievements-page .achievement-card .achievement-gallery .image-grid{display:grid;grid-template-columns:repeat(2, 1fr);gap:1rem}.achievements-page .achievement-card .achievement-gallery .image-grid .image-container{aspect-ratio:4/3;overflow:hidden;border-radius:12px;box-shadow:0 4px 6px -1px rgba(0,0,0,.1),0 2px 4px -1px rgba(0,0,0,.06)}.achievements-page .achievement-card .achievement-gallery .image-grid .image-container img{width:100%;height:100%;object-fit:cover;transition:transform .3s ease}.achievements-page .achievement-card .achievement-gallery .image-grid .image-container img:hover{transform:scale(1.05)}@media(max-width: 768px){.achievements-page .achievement-card .achievement-gallery{margin-top:2rem}}